.content {
  width: 350px;
  padding: 15px;
  background-color: #000;
  border: 5px solid rgba(255, 255, 255, .3);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.todos {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo {
  width: 90%;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 5px;
  font-weight: bold;
  font-size: .8em;
  text-align: left;
}

.add-form, .add-form form {
  width: 100%;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.update-content-form {
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  border-radius: 0%;
  color: #fff;
  background-color: #00f;
  border: none;
  font-weight: 400;
  padding: 5px;
  min-width: 70px;
}

button:active {
  background-color: #f00;
}